body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
  text-shadow: -2px 0 #E53935, 0 0px #E53935, 0px 0 #E53935, 0 -2px #E53935;
  text-align: center;
}

.App-chart-container {
  padding: 15px;
  margin: 0 auto;
  margin-top: 30px;
  max-width: 800px;

  overflow: auto;
}

.App-chart-description {
  font-size: 1.2em;
}

.App-chart-source {
  color: #757575;
  font-size: 0.9em;
  font-style: italic;
  margin-bottom: 30px;
}

.App-chart-source a {
  color: #757575;
}

body{
  background-color: azure
}
header{
  text-align: center;
  font-family: 'Helvetica';
  margin: 0.4rem
}
.Axis path,
.Axis line {
  stroke: #E0E0E0;
}

.Axis text {
  font-size: 11px;
  fill: black;
}

.Axis-Bottom text {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  text-anchor: end;
}

